@-webkit-keyframes titleAnimation { 
	0% { opacity: 0 }
	8% { opacity: 1 }
	17% { opacity: 1 }
	19% { opacity: 0 }
	100% { opacity: 0 }
}
@keyframes titleAnimation { 
	0% { opacity: 0 }
	8% { opacity: 1 }
	17% { opacity: 1 }
	19% { opacity: 0 }
	100% { opacity: 0 }
}

/* Background Images */

.backgroundLoop,
.backgroundLoop:after {
	-webkit-transform: scale(1.05);
	        transform: scale(1.05);
	-webkit-filter: blur(3px);
	        filter: blur(3px);
	position: fixed;
	width: 100%;
	height: 100%;
	top: 0px;
	left: 0px;
	z-index: 0;
}
.backgroundLoop:after {
	content: '';
	background-image: transparent url(/lshaw212/blogat/static/media/home1.e04f4b97.jpg) repeat top left;
}
.backgroundLoop li { list-style: none; }
.backgroundLoop li span {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0px;
	left: 0px;
	color: transparent;
	background-size: cover;
	background-position: 50% 50%;
	background-repeat: none;
	opacity:0;
	z-index: 0;
	/* visibility: hidden; */
	-webkit-animation: imgAnimation 36s linear infinite 0s;
	        animation: imgAnimation 36s linear infinite 0s;
}
.backgroundLoop li:nth-child(1) span {
	background-image: url(/lshaw212/blogat/static/media/home1.e04f4b97.jpg);
}
.backgroundLoop li:nth-child(2) span {
	background-image: url(/lshaw212/blogat/static/media/home2.87f3df92.jpg);
	-webkit-animation-delay: 6s;
	        animation-delay: 6s;
}
.backgroundLoop li:nth-child(3) span {
	background-image: url(/lshaw212/blogat/static/media/home3.ab68996a.jpg);
	-webkit-animation-delay: 12s;
	        animation-delay: 12s;
}
.backgroundLoop li:nth-child(4) span {
	background-image: url(/lshaw212/blogat/static/media/home4.c938e5c0.jpg);
	-webkit-animation-delay: 18s;
	        animation-delay: 18s;
}
.backgroundLoop li:nth-child(5) span {
	background-image: url(/lshaw212/blogat/static/media/home5.2acba292.jpg);
	-webkit-animation-delay: 24s;
	        animation-delay: 24s;
}
.backgroundLoop li:nth-child(6) span {
	background-image: url(/lshaw212/blogat/static/media/home6.811f27cf.jpg);
	-webkit-animation-delay: 30s;
	        animation-delay: 30s;
}

@-webkit-keyframes imgAnimation { 
	0% { opacity: 0; -webkit-animation-timing-function: ease-in; animation-timing-function: ease-in; }
	8% { opacity: 1; -webkit-animation-timing-function: ease-out; animation-timing-function: ease-out; }
	17% { opacity: 1 }
	25% { opacity: 0 }
	100% { opacity: 0 }
}

@keyframes imgAnimation { 
	0% { opacity: 0; -webkit-animation-timing-function: ease-in; animation-timing-function: ease-in; }
	8% { opacity: 1; -webkit-animation-timing-function: ease-out; animation-timing-function: ease-out; }
	17% { opacity: 1 }
	25% { opacity: 0 }
	100% { opacity: 0 }
}
