/* Homepage buttons */
.homepageBtn {
	position: relative;
	/* user-select: none; */
	outline: none !important;
  border: none;
  font-size: 18px;
  transition: color 0.5s, background-color 0.2s, -webkit-transform 0.2s;
  transition: color 0.5s, transform 0.2s, background-color 0.2s;
  transition: color 0.5s, transform 0.2s, background-color 0.2s, -webkit-transform 0.2s;
  outline: none;
  border-radius: 3px;
  margin: 0 10px;
  padding: 23px 33px;
	border: 3px solid transparent;
	background-color: transparent;
  color: rgb(50,50,60);
}
.homepageBtn:active{
	-webkit-transform: translateY(3px);
	        transform: translateY(3px)
}
.homepageBtn::after {
	border-radius: 3px;
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: transparent;
	border: 3px solid #000000;
	opacity: 0;
	z-index: -1;
	-webkit-transform: scaleX(1.1) scaleY(1.3);
	        transform: scaleX(1.1) scaleY(1.3);
	transition: opacity 0.3s, -webkit-transform 0.3s;
	transition: transform 0.3s, opacity 0.3s;
	transition: transform 0.3s, opacity 0.3s, -webkit-transform 0.3s;
}
.homepageBtn::before{
	border-radius: 3px;
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	border: 3px solid rgb(50,50,60);
	transition: opacity 0.3s, border 0.3s;
}
.homepageBtn:hover{
	background-color: transparent;
	box-shadow: none;
	color: #000000;
}
.homepageBtn:hover::before{
	opacity: 0;
}
.homepageBtn:hover::after{
	opacity: 1;
	-webkit-transform: scaleX(1.2) scaleY(1.2);
	        transform: scaleX(1.2) scaleY(1.2);
}

.infoBtn{
	display: block;
	outline: none !important;
	margin: 20px auto;
	max-width: 180px;
	text-decoration: none;
